.index-products {
  padding: 40px 10px;
}

.index-section-title {
  font-size: 26px;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: #fff;
}

.index-product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
}

.index-product-card {
  border: 1px solid #eee;
  border-radius: 16px;
  overflow: hidden;
  text-align: center;
  background-color: #fff;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.index-product-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
}

.index-product-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.index-product-info {
  padding: 13px;
  display: flex;                  /* ✅ flex로 세로 정렬 */
  flex-direction: column;        /* ✅ 세로 방향 */
  align-items: center;           /* ✅ 가운데 정렬 */
  gap: 12px;                     /* ✅ 요소 간 일정 간격 */
  text-align: center;
}

.index-product-name {
  font-size: 16px;
  font-weight: 600;
  margin: 0;                     /* ✅ margin 제거 */
  color: #333;
}

.index-product-price {
  font-size: 16px;
  color: #ff3e3e;
  font-weight: 700;
  margin: 0;                     /* ✅ margin 제거 */
}

.index-btn-detail {
  display: inline-block;
  font-size: 13px;
  padding: 6px 12px;
  background-color: #f1f3f5;
  color: #333;
  border-radius: 20px;
  border: 1px solid #ccc;
  text-decoration: none;
}


.index-btn-detail:hover {
  background-color: #e9ecef;
}

.index-toggle-btn {
  text-align: center;
  margin-top: 24px;
}

.index-toggle-btn button {
  padding: 10px 26px;
  font-size: 15px;
  font-weight: 500;
  border: none;
  background-color: #ff4d4f;
  color: white;
  border-radius: 24px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.index-toggle-btn button:hover {
  background-color: #e04345;
}
